.lesson {
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow-y: clip;
}

.lesson-content {
    height: 100vh;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    scroll-snap-type: y mandatory;
    scroll-snap-align: start;
    overflow-y: scroll;
}

.lesson-content h1 {
    font-family: Georgia, serif;
    margin-top: 1rem;
}

.lesson-content section {
    min-height: 100vh;
    scroll-snap-align: start;
}

.lesson-navigation-arrows {
    position: absolute;
    margin: 30px;
    bottom: 0;
    right: 0;
    align-items: center;
    cursor: pointer;
}

.lesson-navigation-arrows p {
    font-size: 1.5rem;
    color: rgba(92, 92, 92, 0.82);
    /*margin-bottom: 3px;*/
    margin: 0;
}

.lesson-navigation-arrow {
    background-color: transparent;
    border: none;
    margin: 0;
    padding: 0;
}

.lesson-navigation-arrow svg {
    color: rgba(92, 92, 92, 0.82);
    width: 100px;
    height: 100px;
}
