#highlight {
    display: none;
    width: 100%;
    object-fit: contain;
}

#think {
    display: none;
    width: 100%;
    object-fit: contain;
}

.result-image-line {
    display: none
}

#analyzing-articles-images h2 {
    display: none;
}

#result-image {
    width: 100%;
    display: none;
    flex-direction: column;
}

.animate #think {
    display: initial;
    animation-duration: 2s;
    animation-name: thinkmovein;
}

.animate #highlight {
    display: initial;
    animation-duration: 2s;
    animation-name: fadein;
}

.animate .result-image-line {
    display: initial;
    animation-duration: 2s;
    animation-name: grow-line;
    animation-timing-function: ease-in-out;
}

.animate #result-image {
    display: flex;
}

#analyzing-articles-images .animate h2 {
    display: initial;
    animation-duration: 2s;
    animation-name: fadein;
}

@keyframes thinkmovein {
    from {
        margin-left: -100%;
        margin-right: 100%;
    }

    to {
        margin-left: 0;
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 100%;
    }
}

@keyframes grow-line {
    from {
        width: 10%;
    }

    to {
        width: 100%
    }
}

.layout-grid div {
    display: flex;
}

.vertical-flex {
    display: flex;
    flex-direction: column;
}

.activity-header {
    font-size: max(1rem, 2.5vw);
    color: #606060
}

.activity-description {
    font-size: max(1.3rem, 1.5vw);
}

#analyzing-articles h2 {
    color: var(--cerulean-blue);
    font-size: calc(1rem + 1.5vw);
}

h2#analyzing-articles-header {
    /*color: var(--cerulean-blue);*/
    font-family: "Nunito", sans-serif;
    font-weight: 700;
    font-size: max(2rem, 5vw);
}



#analyzing-articles-images .vertical-flex {
    gap: max(0.5rem, 0.75vw);
}

.result-image-section {
    display: flex;
    width: 100%;
    align-items: center;
    margin: 2px;
}

.result-image-line {
    width: 100%;
    background-color: white;
    height: 0.4rem;
    margin-left: 0.2rem;
    border-radius: 0.2rem;
}

#discussion .container {
    padding-top: 2.5rem;
}

#conclusion .container {
    padding-top: 2.5rem;
}

ul {
    list-style: none;
}

ul li {
    font-family: Arvo, sans-serif;
    font-size: 2rem;
}

ul li:not(:last-child) {
    margin-bottom: 1rem;
}